.skills-section {
  background-color: #121212;
  color: white;
  padding: 2rem;
  border-radius: 10px;
  margin: 2rem 0;
  text-align: center;
}

.skills-list {
  display: flex;
  gap: 1rem;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 900px;
  margin: 0 auto;
}

.skills-list span {
  background-color: #0073e6; /* 파란색 배경 */
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  font-size: 1rem;
}

