.header-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #121212;
  padding: 2rem;
  color: white;
  flex-wrap: wrap;
  text-align: center;
}

.header-content {
  flex: 1;
}

.profile-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 150px;
}

.header-content h1 {
  font-size: 2.5rem;
  margin: 0;
  color: #0073e6; /* 파란 텍스트 */
}

.header-content p {
  margin-top: 0.5rem;
  font-size: 1.2rem;
}

.profile-pic {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  object-fit: cover;
  border: 3px solid #0073e6; /* 파란색 테두리 */
}

@media (max-width: 768px) {
  .header-section {
    flex-direction: column;
  }

  .profile-wrapper {
    margin-top: 1rem;
  }
}

