.projects-section {
  background-color: #1e1e1e;
  color: white;
  padding: 2rem;
  border-radius: 10px;
  margin: 2rem 0;
}

.project-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
  justify-items: center;
  max-width: 1200px;
  margin: 0 auto;
}

.project-card {
  background-color: #121212;
  padding: 1.5rem;
  border-radius: 10px;
  border: 1px solid #0073e6;
  width: 100%;
  max-width: 400px;
}

.project-card h3 {
  color: #0073e6;
}

.project-card p {
  color: #f0f0f0;
}

